import { Transfer } from 'antd'
import Element from '../../core/element/Element'
import { staticMembers } from '../../core/decorator'
import { formControl } from './Form'
import ElementCategory from '../../core/element/ElementCategory'

// 
const mockData = []
for (let i = 0; i < 20; i++) {
  mockData.push({
    key: i.toString(),
    title: `content${i + 1}`,
    description: `description of content${i + 1}`,
    disabled: i % 3 < 1,
  })
}

@staticMembers({
  displayName: '',
  category: ElementCategory.FORM,
  type: 'transfer',
  icon: 'cluster',
  props: [{
    name: 'label',
    type: 'input',
    props: {
      label: '',
    },
    default: '',
  }, {
    name: 'dataSource',
    type: 'json',
    props: {
      label: ''
    },
    default: mockData,
    dataType: ''
  }, {
    name: 'leftLabel',
    type: 'input',
    props: {
      label: '',
    },
    default: '',
  }, {
    name: 'rightLabel',
    type: 'input',
    props: {
      label: '',
    },
    default: '',
  }]
})
class InnerCls extends Element {
  prepareProps(props: any): any {
    const { leftLabel, rightLabel, value, ...innerProps } = props
    return {
      ...innerProps,
      titles: [ leftLabel, rightLabel ],
      targetKeys: value,
    }
  }

  @formControl
  _getComponent() {
    return Transfer
  }
}

export default InnerCls
