import React from 'react'
import { Radio } from 'antd'
import Element from '../../core/element/Element'
import ElementCategory from '../../core/element/ElementCategory'
import { staticMembers/*, canBeDroped, canDropTo*/ } from '../../core/decorator'
import { formControl } from './Form'

@staticMembers({
  displayName: '',
  category: ElementCategory.FORM,
  type: 'radio',
  icon: 'tablet',
  props: [{
    name: 'label',
    type: 'input',
    props: {
      label: '',
    },
    default: '',
  }, { // 
    name: 'options',
    type: 'list',
    props: {
      label: ''
    },
    default: [
      { value: 0, label: ''},
      { value: 1, label: ''},
      { value: 2, label: ''},
    ],
    dataType: 'string'
  }, {
    name: 'buttonStyle',
    type: 'radio',
    props: {
      label: '',
      options: [
        { value: '', label: ''},
        { value: 'solid', label: 'Solid'},
        { value: 'outline', label: 'Outline'},
      ],
      buttonStyle: 'solid',
    },
    default: '',
  }]
})
// @canDropTo('row') // 
// @canBeDroped('!col') // 
export default class extends Element {
  @formControl
  _getComponent() {
    return function(props: any) {
      const { options = [], ...other } = props

      return props.buttonStyle ? (
        <Radio.Group {...other} >
          {
            options.map((item: any) => (
              <Radio.Button value={item.value} key={item.value}>{item.label}</Radio.Button>
            ))
          }
        </Radio.Group>
      ) : (
        <Radio.Group {...props} />
      )
    }
  }
}
