import React from 'react'
import { Select } from 'antd'
import Element from '../../core/element/Element'
import { staticMembers } from '../../core/decorator'
import { formControl } from './Form'
import ElementCategory from '../../core/element/ElementCategory'

const { Option } = Select

@staticMembers({
  displayName: '',
  category: ElementCategory.FORM,
  type: 'select',
  icon: 'select',
  props: [{
    name: 'label',
    type: 'input',
    props: {
      label: '',
    },
    default: '',
  }, { // 
    name: 'options',
    type: 'list',
    props: {
      label: ''
    },
    default: [
      { value: 0, label: '' },
      { value: 1, label: '' },
      { value: 2, label: '' },
    ],
    dataType: 'string'
  }]
})
class InnerCls extends Element {
  @formControl
  _getComponent() {
    return function(props: any) {
      const { options = [], ...innerProps} = props

      return (
        <Select {...innerProps}>
          {
            options.map((item: any) => (
              <Option key={item.value} value={item.value}>{item.label}</Option>
            ))
          }
        </Select>
      )
    }
  }
}

export default InnerCls
