import React from 'react'
import { Switch } from 'antd'
import Element from '../../core/element/Element'
import ElementCategory from '../../core/element/ElementCategory'
import { staticMembers/*, canBeDroped, canDropTo*/ } from '../../core/decorator'
import { formControl } from './Form'

@staticMembers({
  displayName: '',
  category: ElementCategory.FORM,
  type: 'switch',
  icon: 'switcher',
  props: [{ // 
    name: 'label',
    type: 'input',
    props: {
      label: ''
    },
    default: '',
    dataType: 'string'
  }, {
    name: 'size',
    type: 'radio',
    props: {
      label: '',
      options: [
        { label: 'M', value: 'default' },
        { label: 'S', value: 'small' }
      ],
      buttonStyle: 'solid'
    },
    default: 'default',
    dataType: 'string'
  }]
})
// @canDropTo('row') // 
// @canBeDroped('!col') // 
export default class extends Element {
  @formControl
  _getComponent() {
    return class extends React.Component<any> {
      render() {
        const { value, ...other } = this.props
        const props = { ...other, checked: !!value }

        return (
          <Switch {...props} />
        )
      }
    }
  }

  // prepareProps(props: any) {
  //   const { value, ...other } = props
  //   return { ...other, checked: !!value }
  // }
}
