import { Row } from 'antd'
import Element from '../../core/element/Element'
import ElementCategory from '../../core/element/ElementCategory'
import { canBeDroped, staticMembers, canDropTo } from '../../core/decorator'

@staticMembers({
  displayName: '()',
  category: ElementCategory.LAYOUT,
  type: 'row',
  icon: 'column-width',
  props: [{
    name: 'align',
    type: 'select',
    props: {
      label: '',
      options: [
        { label: '', value: 'top' },
        { label: '', value: 'middle' },
        { label: '', value: 'bottom' }]
    },
    default: 'middle',
    dataType: 'string'
  }]
})
@canBeDroped('col')
@canDropTo('!row') // 
class InnerCls extends Element {
  _getComponent() {
    return Row
  }
}

export default InnerCls
